import React from 'react'
import DesktopNavbar from '../desktopNavbar/DesktopNavbar'
import MobileNavbar from '../mobileNavbar/MobileNavbar'

function Navbar() {
  return (
    <div>
      <DesktopNavbar />
      <MobileNavbar />
    </div>
  )
}

export default Navbar



